<template>
<h2 hidden>{{state.goalid = $route.params.id }}</h2>
<div class="row">
  <div class="col-lg-3 col-md-12"></div>
  <div class="card col-lg-6 col-md-12">
    <div class="card-header">{{state.name}}</div>
    <div class="card-body">
      <h5 class="card-title">{{state.dueDate}}까지, {{state.successQ}}{{state.unit}}목표</h5>
      <p class="card-text">{{state.content}}</p>
      <a href="#" class="btn btn-success" @click="$router.go(-1)">뒤로가기</a>
      <button class="btn btn-dark" @click="state.deleteModal = true">목표 삭제</button>
    </div>
  </div>
  <div class="col-lg-3 col-md-12"></div>
</div>
<Teleport to="body">
    <modal :show="state.deleteModal" @close="state.deleteModal = false">
      <template #header>
        <h2 style="color: red;">나의 목표 삭제</h2>
      </template>
      <template #body>
        <div>
          <h5>정말 {{ state.name }} 목표를 삭제하시겠습니까?</h5>
        </div>
      </template>
      <template #footer>
        <el-button type="primary" plain @click="state.deleteModal = false">취소</el-button>
        <el-button type="primary" plain @click="deleteGoal">목표 삭제하기</el-button>
      </template>
    </modal>
  </Teleport>
</template>
<script>
import { ref, reactive } from '@vue/reactivity'
import { onMounted } from 'vue'
import { useRouter } from 'vue-router';
import Modal from './teleport/Modal.vue'

import axios from 'axios'
export default {
  components: { Modal },

  setup() {
    onMounted(() => { getGoal() })
    const router = useRouter();
    const state = reactive({
      goalid: '',
      name: '',
      content: '',
      successQ: '',
      dueDate: '',
      unit: '',
      deleteModal: false,
      email: sessionStorage.getItem('email'),
      token: sessionStorage.getItem("TOKEN"),
    })
    const getGoal = async () => {

      const url = "http://localhost:9090/api/goalread"
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token
      }
      const body = { goalid: state.goalid }
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.status === 200) {
          state.name = res.data.dto.name
          state.content = res.data.dto.content
          state.successQ = res.data.dto.successQ
          state.dueDate = res.data.dto.dueDate
          state.unit = res.data.dto.unit
        }
      })
    }
    const deleteGoal = async () => {

      const url = "http://localhost:9090/api/goalDelete"
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token
      }
      const body = { goalid: state.goalid, }
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.status === 200) {
          alert(state.name +"목표가 삭제 되었습니다."); state.deleteModal=false; router.push({ name: "privateProfile" });
        }
      })
    }
    return { getGoal, state, deleteGoal }
  },
}
</script>

<style scoped>
img {
  width: 2em;
}

input:focus {
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.25);
  transition: all 0.2s;
  border: 1px solid rgba(6, 90, 92, 0.5);
}

textarea:focus {
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.25);
  transition: all 0.2s;
  border: 1px solid rgba(6, 90, 92, 0.5);
}

.btn {
  margin: 0.5em;
}
</style>
